/* Position the image box (needed to position the left and right arrows) */
.box {
  position: relative;
  margin-top: 23px;
}

/* Hide the images by default */
/* .mySlides {
    display: none;
  } */

/* Add a pointer when hovering over the thumbnail images */
.cursor {
  cursor: pointer;
}

/* Next & previous buttons */
.prev,
.next {
  background-color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
  position: absolute;
  top: 54%;
  width: auto;
  padding: 16px;
  margin-top: -50px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  border-radius: 0 3px 3px 0;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

/* Position the "next button" to the right */
.next {
    border-radius: 38px 0 0 38px;
    width: 31px;
}

.prev{
  width: 31px!important;
    border-radius: 0px 38px 38px 0px!important;
}

/* On hover, add a black background color with a little bit see-through */

.main-img:hover .prev,
.main-img:hover .next {
  background-color: rgba(255, 0, 0, 0.3);
  visibility: visible;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* box for image text */
.caption-box {
  text-align: center;
  padding: 2px 16px;
  color: white;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Six mini-images side by side */
.mini-image {
  float: left;
  width: 16.66%;
  margin: 10px 0px 0px 10px;
}

/* Add a transparency effect for thumnbail images */
.demo {
  opacity: 0.6;
}

.demo:hover {
  opacity: 1;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.dot:hover {
  background-color: #717171;
}
/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

