.box-search {
    width: 300px;
    float: left;
  }
  .box-search .cps-group-input {
    position: relative;
  }
  .input-group-btn {
    width: 26px;
    height: 34px;
    display: flex;
    align-items: center;
    float: left;
    padding: 0.5rem 0 0.5rem 1rem;
    background: #ffffff;
    border-radius: 1rem 0 0 1rem;
    font-size: 1.1rem;
    color: #707070;
    position: relative;
    z-index: 3;
    white-space: nowrap;
  }
  .close-group-btn {
    width: 26px;
    height: 34px;
    display: flex;
    align-items: center;
    float: right;
    padding: 0.5rem 0 0.5rem 0.5rem;
    background: #ffffff;
    border-radius:  0 1rem 1rem 0;
    font-size: 1.1rem;
    color: #707070;
    position: absolute;
    z-index: 3;
    white-space: nowrap;
    top: 0px;
    right:0px;
    
  }
  .close-group-btn .fas{
    cursor: pointer;
  }
  .cps-input {
    width: calc(100% - 26px);
    padding: 0.5rem 1rem 0.5rem 0.6rem;
    border: 0;
    border-radius: 0 1rem 1rem 0 !important;
    box-shadow: none !important;
    position: relative;
    z-index: 2;
  }
  .cps-input {
    height: 34px;
    padding: 0.4rem 1rem 0.6rem 1rem;
    border: 0;
    outline: none;
    font-size: 1rem;
  }
  header.cps-header
    nav.cps-navbar
    .cps-container
    .box-search
    + .box-search-result {
    width: 100% !important;
    min-height: 44px;
    max-height: 500px;
    padding: 10px 0 0 0;
    border-radius: 0 0 10px 10px;
    background: #ffffff;
    box-shadow: rgb(60 64 67 / 10%) 0px 1px 2px 0px,
      rgb(60 64 67 / 15%) 0px 2px 6px 2px !important;
    position: absolute;
    top: 24px !important;
    left: 0 !important;
    z-index: 1;
    overflow: hidden;
    /* overflow-y: scroll; */
  }
  .box-search + .box-search-result ul {
    width: 100%;
    position: absolute;
    background-color: white;
    margin: 0;
    padding: 0;
    border-radius: 15px;
  }
  .box-search + .box-search-result ul > li {
    display: flex;
    padding: 10px 10px;
    border: 0;
  }
  .box-search + .box-search-result ul > li {
    display: flex;
    padding: 10px 10px;
    border: 0;
  }
  .box-search-result.search-autocomplete
    .search_suggest_data
    > li[style="display:block"] {
    display: none !important;
  }
  .box-search + .box-search-result ul > li > a {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  .box-search + .box-search-result ul > li .box-img {
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px 0 0;
  }
  ul.search_suggest_data li a img {
    max-width: 100%;
    max-height: 72.33px;
  }
  .box-search + .box-search-result ul > li .box-img img {
    width: 100%;
  }
  .box-search + .box-search-result ul > li .box-info {
    width: calc(100% - (60px + 10px));
    float: left;
  }
  .box-search + .box-search-result ul > li .box-price {
    width: 100%;
    display: flex;
    float: left;
  }
  .box-search + .box-search-result ul > li .box-name p {
    margin-bottom: 5px;
    font-size: 0.8rem;
    font-weight: 700;
    line-height: 1.4;
    color: #444444;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .box-search + .box-search-result ul > li .box-price .special-price {
    display: inline-block;
    font-size: 12px;
    font-weight: 700;
    line-height: 1.4;
    color: #d70018;
  }
  .box-search + .box-search-result ul > li .box-price .old-price {
    display: inline-block;
    margin: 0 0 0 10px !important;
    float: left;
    font-size: 10px;
    line-height: 1.4;
    text-decoration: line-through;
    color: #707070;
  }
.social {
  background: #f5f5f5 0% 0% no-repeat;
  box-shadow: 0 4px 6px #00000029;
  border-radius: 30px;
  display: inline-block;
  padding: 2px;
  position: fixed;
  top: 45%;
  z-index: 5;
}
.social ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.social ul li a.blue {
  color: #3c5a99;
}
.social ul li  {
    padding: 3px 0 3px 0;
  }
.social ul li a {
  display: block;
  width: 40px;
  height: 30px;
  text-align: center;
  font-size: 24px;
  padding: 5px 0 5px 0;
  border-radius: 30px;
}
.social ul li a.red {
  color: #f00;
}
.social ul li a.rainbow {
  display: block;
  padding: 2px;

  width: 40px;
  height: 40px;
  font-size: 26px;
}
.social ul li a.rainbow span {
  background: #fff;
  display: block;
  padding: 4px 0 2px 0;
  display: block;
}
.social ul li a.rainbow span i {
  background: transparent
    linear-gradient(
      50deg,
      #fbe18a 0%,
      #fcbb45 21%,
      #f75274 38%,
      #d53692 52%,
      #8f39ce 74%,
      #ad63a0 100%
    )
    0% 0% no-repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: block;
}

